her.\n        ")])]),_c('div',{staticClass:"pixl-dogs-v1-container3"},[_c('span',{staticClass:"pixl-dogs-v1-text02"},[_vm._v("\n          At PixlDogs weve been working on a different approach to NFT\n          collectables and working on how such valuations work. Being a\n          collectable our main objective is to create just that. Rather than\n          focusing on rare attributes and traits, weve designed our NFTs to\n          have a collectable appeal. Different asset combination, as well as\n          different breed sizes help create different scenarios. Some PixlDogs\n          have appeared under rare circumstances like in a moon night while\n          some in a sunny day. Whether youre a fan of Pugs or Golden\n          Retrievers, everyone has their favourite dog breed and by collecting\n          more NFTs you increase your chances of getting your favourite breed\n          with your favourite scenario.\n        ")])]),_c('div',{staticClass:"pixl-dogs-v1-container4"},[_c('a',{staticClass:"pixl-dogs-v1-link2 anchor button",attrs:{"href":"https://medium.com/@pixldogs/who-let-the-dogs-out-e03f4ae619f","target":"_blank","rel":"noreferrer noopener"}},[_vm._v("\n          READ MORE\n        ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('a',{staticClass:"pixl-dogs-v1-link3",attrs:{"href":"https://opensea.io/collection/pixldogs","target":"_blank","rel":"noreferrer noopener"}},[_c('h1',{staticClass:"pixl-dogs-v1-heading1 section-Heading"},[_vm._v("\n          HOLDING A V1 PIXLDOG\n        ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('h1',{staticClass:"pixl-dogs-v1-text11"},[_c('span',[_vm._v("OPENSEA LIVE STATS")])])}]

