import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'

import MintingSite from './views/minting-site'
import PixlDogsV2 from './views/pixl-dogs-v2'
import PixlDogsV1 from './views/pixl-dogs-v1'
import Home from './views/home'
import './style.css'

Vue.use(Router)
Vue.use(Meta)
export default new Router({
  mode: 'history',
  routes: [
    {
      name: 'Minting Site',
      path: '/minting-site',
      component: MintingSite,
    },
    {
      name: 'PixlDogs V2',
      path: '/pixl-dogs-v2',
      component: PixlDogsV2,
    },
    {
      name: 'PixlDogs V1',
      path: '/pixl-dogs-v1',
      component: PixlDogsV1,
    },
    {
      name: 'Home',
      path: '/',
      component: Home,
    },
  ],
})
